import React, { Component } from 'react'
import ThreadCard from './ThreadCard'

class Feed extends Component {
  constructor(props) {
    super(props)
    this.state = {
      threads: [
        {
          title: "Test1",
          time: "2:10",
          author: "admin",
          votes: 10,
          replies: 4
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        },
        {
          title: "test2",
          time: "2:20",
          author: "admin2",
          votes: 8,
          replies: 1
        }
      ],
      feed: true,
      events: false,
      people: false,
    }
  }

  renderThread(thread, index) {
    return (
      <div key={thread + index} >
        <ThreadCard info={thread} key={"tcard" + thread + index} />
        {this.state.threads[index + 1] ? <hr key={"hr" + thread + index} /> : null}
      </div>
    )
  }

  render() {
    return (
      <div className="feed-container">
        <div className="button-group">
          <button className="feed-button"> feed </button>
          <button className="feed-button feed-button-inactive"> events </button>
          <button className="feed-button feed-button-inactive"> people </button>
        </div>
        <div className="feed">
          {this.state.threads.map((thread, index) => this.renderThread(thread, index))}
        </div>
      </div>
    )
  }
}

export default Feed