import React, { Component } from 'react'
import PropTypes from "prop-types"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class ThreadCard extends Component {
  render() {
    return (
      <div className="card thread-card">
        {/* <div className="flex flex-column mr-3">
          <FontAwesomeIcon className="mb-1" icon="chevron-circle-up"></FontAwesomeIcon>
          <h4>123</h4>
          <FontAwesomeIcon className="mt-1" icon="chevron-circle-down"></FontAwesomeIcon>
        </div> */}
        <div className="thumb">
          {/* <img src={Image} alt="thumbnail"></img> */}
        </div>
        <div className="thread-text ml-3">
          <h2>{this.props.info.title}</h2>
          <div className="flex">
            <p> 1h ago by&nbsp; </p>
            <button>{this.props.info.author}</button>
          </div>
          <div className="flex">
            <button>{this.props.info.replies} {this.props.info.replies === 1 ? "reply" : "replies"}</button>
            <button>report</button>
          </div>
        </div>
      </div>
    )
  }
}

ThreadCard.propTypes = {
  info: PropTypes.object.isRequired
}

export default ThreadCard