import React, { Component } from 'react'
import mapboxgl from 'mapbox-gl';
import '../../styles/Map.css'

mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_KEY;

class MBMap extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lng: -76.9386,
      lat: 38.9859,
      zoom: 15,
      style: 'mapbox://styles/taxidermied-potato/ck7yawfpb0hrn1ipqz2v8dm6x'
    };
  }

  updatePos(pos) {
    this.setState({lng: pos.coords.longitude, lat: pos.coords.latitude})
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: this.state.style,
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom
    });
  }

  defaultPos() {
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: this.state.style,
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom
    });
  }

  componentDidMount() {
    const geo = navigator.geolocation

    geo.getCurrentPosition((pos) => this.updatePos(pos), this.defaultPos())

    // map.dragRotate.disable();
    
    // map.on('move', () => {
    //   this.setState({
    //     lng: map.getCenter().lng.toFixed(4),
    //     lat: map.getCenter().lat.toFixed(4),
    //     zoom: map.getZoom().toFixed(2)
    //   });
    // });
  }

  render() {
    return (
      <div>
        <div className="mapContainer" ref={el => this.mapContainer = el} />
      </div>
    )
  }
}

export default MBMap