import React, { useContext } from 'react'
import '../styles/Landing.css'
import LandingImage from '../images/map-min.png'

import FBase from '../firebase'
import { HashRouter as Router, Link } from 'react-router-dom'
import { withRouter } from 'react-router'
import { AuthContext } from '../auth/Auth'
import LazyLoad from 'react-lazyload';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import WLogo from '../images/logo.svg'

function Landing() {
  const { currentUser } = useContext(AuthContext)

  return (
    <div>
      <header className="py-2 pt-3">
        <nav className="landing-nav mx-auto px-4">
          <span className="flex transition-up">
            <img src={WLogo} className="wlogo mx-2" />
            <h1 className="mr-2"> wander </h1>
          </span>
          <span className="mx-2 hline"> | </span>
          <a className="mx-2 transition-up" href="./"> Download </a>
          <a className="mx-2 transition-up" href="./"> Docs </a>
          <a className="mx-2 transition-up" href="./"> Community </a>
          {!currentUser ?
            <Link to="/login" className="ml-auto">
              <button className="outline-button transition-up">
                login <FontAwesomeIcon className="ml-1" icon="sign-in-alt" />
              </button>
            </Link>
            :
            <div className="ml-auto flex">
              <Link to="/app">
                <button className="outline-button mr-2 transition-up">
                  wander <FontAwesomeIcon className="ml-1" icon="globe-americas" />
                </button>
              </Link>
              <button className="outline-button transition-up" onClick={() => FBase.auth().signOut()}>
                logout
              </button>
            </div>
          }
          <span className="mx-2 ml-3 hline"> | </span>
          <FontAwesomeIcon icon="language" className="mx-2 py-1 med-icon transition-up" />
        </nav>
        <nav className="mobile-nav mx-auto px-4">
          <img src={WLogo} className="wlogo mx-2" />
          <h1 className="mr-2"> wander </h1>
          {!currentUser ?
            <Link to="/login" className="ml-auto">
              <button className="outline-button">
                login <FontAwesomeIcon className="ml-1" icon="sign-in-alt" />
              </button>
            </Link>
            :
            <div className="ml-auto flex">
              <Link to="/app">
                <button className="outline-button mr-2">
                  wander <FontAwesomeIcon className="ml-1" icon="globe-americas" />
                </button>
              </Link>
              <button className="outline-button" onClick={() => FBase.auth().signOut()}>
                logout
            </button>
            </div>
          }
        </nav>
      </header>

      <div className="content-section py-55">
        <h2 className="mb-2"> for the lost, found, and in-betweens. </h2>
        <p className="mx-5 mb-4 headline"> A geolocative message board, based in every pocket of the world. Stay comfy near home or take a peek past undiscovered horizons. </p>
        <div className="button-group">
          <button className="px-4 mx-2 fill-button transition-up"> <FontAwesomeIcon icon="code" className="active mr-2 button-icon" /> Open Web App </button>
          <button className="px-4 mx-2 fill-button transition-up"> <FontAwesomeIcon icon="desktop" className="active mr-2 button-icon" /> Download for Windows </button>
        </div>
        <LazyLoad once>
          <img className="map-img" src={LandingImage} alt="landing"></img>
        </LazyLoad>
      </div>

      <footer className="mb-4">
        <div className="footer-menu py-4 px-5">
          <div className="socials ml-4">
            <FontAwesomeIcon className="transition-up" icon={["fab", "facebook-square"]} />
            <FontAwesomeIcon className="transition-up" icon={["fab", "instagram"]} />
            <FontAwesomeIcon className="transition-up" icon={["fab", "youtube-square"]} />
            <FontAwesomeIcon className="transition-up" icon={["fab", "github"]} />
          </div>
          <div className="footer-nav ml-4">
            <div className="mx-3 mb-3">
              <h3 className="mb-1"> product </h3>
              <ul>
                <li> <a href="./">web</a> </li>
                <li> <a href="./">desktop</a> </li>
                <li> <a href="./">ios</a> </li>
                <li> <a href="./">android</a> </li>
              </ul>
            </div>
            <div className="px-3 mb-3">
              <h3 className="mb-1"> for organizations </h3>
              <ul>
                <li> <a href="./">schools</a> </li>
                <li> <a href="./">communities</a> </li>
                <li> <a href="./">businesses</a> </li>
              </ul>
            </div>
            <div className="px-3 mb-3">
              <h3 className="mb-1"> resources </h3>
              <ul>
                <li> <a href="./">documentation</a> </li>
                <li> <a href="./">api</a> </li>
                <li> <a href="./">support</a> </li>
                <li> <a href="./">privacy/terms</a> </li>
              </ul>
            </div>
            <div className="px-3 mb-3">
              <h3 className="mb-1"> company </h3>
              <ul>
                <li> <a href="./">about us</a> </li>
                <li> <a href="./">news</a> </li>
                <li> <a href="./">jobs</a> </li>
              </ul>
            </div>
            <div className="flex-spacer">
            </div>
          </div>
        </div>        
      </footer>
    </div>
  )
}

export default withRouter(Landing)