import React, { useState, useEffect } from 'react'
import '../styles/AppHome-Wood.css'

import FBase from '../firebase'
import { HashRouter as Router, Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { CSSTransition } from "react-transition-group";
import Feed from '../components/Feed'
import Map from '../components/Map/MBMap'
import WLogo from '../images/logo.svg'

const AppHome = () => {
    const [inProp, setInProp] = useState(false)

    useEffect(() => {
        setInProp(true)
    }, [])

    return (
        <div className="flex">
            <div className="float-right gmap">
                <Map />
            </div>
            <CSSTransition
                in={inProp}
                timeout={300}
                classNames="page"
                unmountOnExit
            >
                <div className="side-container">
                    <header>
                        <nav className="feed-nav px-4">
                            <Link to="./" target="_blank">
                                <span className="logo-rel flex transition-up">
                                    <img src={WLogo} className="wlogo mr-2" />
                                    <h1> wander </h1>
                                </span>
                            </Link>
                            <span className="hline ml-3"> | </span>
                            <input className="mx-2 feed-search" placeholder="Search the feed">
                            </input>
                            <FontAwesomeIcon className="ml-auto transition-up" icon="bars" />
                        </nav>
                    </header>
                    <Feed />
                    <span className="flex mx-auto mt-4">
                        <h3> Theme: Woodcut </h3>
                        <h4> &nbsp; - Flat Light </h4>
                        <h4> &nbsp; - Flat Dark </h4>
                    </span>
                    <button className="sign-out transition-up" onClick={() => FBase.auth().signOut()}> sign out </button>
                </div>
            </CSSTransition>
        </div>
    )
}

export default AppHome
